@use "variables" as v;

.text-center{
    text-align: center;
}

.calendarTitulo{
    font-size: 1.4rem;
    text-align: center;
    color: v.$negro;
}
.calendarUbicacion{
    font-size: 1.4rem;
    text-align: center;
    color: v.$negro;
}
.calendarHorario{
    font-size: 1.2rem;
    font-weight: bold;
    text-align: center;
    margin-top: 1.4rem;
    color: v.$negro;
}
.fc-col-header-cell{
    font-size: 2rem;
}
.calendarMargin{
    margin-left: 15% !important;
}
.fc .fc-daygrid-day.fc-day-today{
    background-color: #b4c0cc !important;
}

.fc .fc-toolbar.fc-header-toolbar{
    margin-top: 4rem !important;
}
.fc .fc-daygrid-day-number{
    font-size: 1.2rem;
    font-weight: bold;
}
.fc-theme-standard td, .fc-theme-standard th{
    border: 2px solid #3a3491;
}
.fc .fc-scrollgrid table{
    border-left-style: solid !important;
    border-right-style: hidden !important;
    border-top-style: solid !important;
    border-bottom-style: hidden !important;
    border-left-color: #3a3491 !important;
    border-top-color: #3a3491 !important;
}
.fc .fc-daygrid-event-harness{
    padding: 0.2rem !important;
}