@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.crearInvitacion{
    background-color: v.$blanco;
    padding: 4rem;
    box-shadow: 10px 10px 8px -3px rgba(0,0,0,0.33);
    #linea{
        max-width: 85%;
        border-top: 2px solid v.$gris;
        min-height: 0;
        margin-top: 2rem;
        padding: 1rem 0;
    }
    .crear{
        label{
            font-size: 1.6rem;
        }
        .confirm__btn{
            @include m.button(v.$primario, v.$primario-hover, v.$blanco, v.$blanco-hover);
        }
        @include m.tablet{
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        .Formulario{
            input, textarea, label, select{
                min-width: 30rem;
                @include m.tablet{
                    min-width: 50rem;
                }
            }
        }
    }
    .enviar{
        margin-top: 3rem;
        .__Tabla{
            max-width: 90%;
            margin-top: 2rem;
            margin-bottom: 2rem;
            .tabla {
                .tabla__cabecera {
                    .cabecera {
                        width: fit-content;
                        padding: 1rem;
                        text-align: center;
                        vertical-align: middle;
                        background-color: v.$tabla-cabecera;
                        color: v.$tabla-cabecera-txt;
                    }
                    .cabecera:hover {
                        background-color: v.$tabla-cabecera-hover;
                    }
                }
                .tabla__cuerpo {
                    tr {
                        .nombreInvitado,
                        .correoInvitado{
                            font-size: 1.3rem;
                            text-align: center;
                            vertical-align: middle;
                            padding: 1rem 4rem 1rem 4rem;
                        }
                    }
                }
    
            }
        }
        .enviar__flex{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .boton{
                display: flex;
                align-items: center;
                justify-content: center;
                .aggInvitado__btn{
                    @include m.button(v.$secundario, v.$secundario-hover, v.$blanco, v.$blanco-hover);
                }
            }
        }
        label{
            font-size: 1.6rem;
        }
        
        @include m.tablet{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
        .Formulario{
            input, textarea, label, select{
                min-width: 30rem;
                @include m.tablet{
                    min-width: 10rem;
                }
            }   
        }
        .containerFlex{
            display: flex;
            flex-direction: column;
            .form-group{
                @include m.tablet{
                    min-width: 25rem;
                    max-width: 100%;
                }
            }
            .Selecccion{
                min-width: 30rem;
                @include m.tablet{
                //    width: 10rem;
                   min-width: 0;
                }
            }
            @include m.tablet{
                flex-direction: row;
            }
        }
    }
    .containerListaInvitados{
        .campo{
            display: flex;
            gap: 4rem;
            justify-content: center;
            align-items: center;
            text-align: center;
            margin-bottom: 1rem;
            p{
                font-size: 1.4rem;
                min-width: 12.9rem;
            }
        }
    }
}