@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.registrarInforPersonal {
    box-sizing: border-box;
    transition: margin-left 0.3s ease-in-out, width 0.3s ease-in-out;

    @media (max-width: 768px) {
        margin-left: 0;
        width: 100%;
        margin-bottom: 9rem;
    }

    .cRegistro {
        padding: 20px;
        background-color: #ffffff;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
        border-radius: 8px;
        margin: 20px;
        height: auto;

        @media (min-width: 768px) {
            max-width: calc(100% - 40px);
            margin-top: 20px;
            margin-bottom: 20px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }

        .Formulario {
            text-align: left;
            background-color: v.$blanco;

            @include m.tablet {
                display: flex;
                flex-direction: column;
                justify-content: center;
                width: 90%;
                text-align: left;
            }

            .hidden {
                display: none;
            }

            .informacionPersonal {
                .personal__titulo {
                    margin-bottom: 1rem;
                    font-size: 2rem;

                    @include m.tablet {
                        font-size: 2.5rem;
                    }
                }

                .form-group {
                    @include m.tablet {
                        max-width: 40rem;

                        input,
                        select {
                            min-width: 0;
                        }

                        select {
                            min-height: 4rem;
                        }
                    }
                }

                .NombreCompleto {
                    width: 100%;
                    display: flex;
                    justify-content: space-between;
                    flex-wrap: wrap;

                    .form-group {
                        flex: 1;
                        min-width: 30%; 

                        &:last-child {
                            margin-right: 0;
                        }

                        @include m.tablet {
                            min-width: 25rem;
                            margin-right: 1rem;
                        }
                    }

                    .inputControl {
                        @include m.input(v.$blanco, #ecedec, v.$primario, v.$gri);
                    }

                    .labelControl {
                        font-size: 1.6rem;

                        @include m.tablet {
                            font-size: 2rem;
                            max-width: calc(100% - 40px);

                        }
                    }
                }

                .contacto,
                .identificacion {
                    display: flex;
                    justify-content: space-between;
                    flex-wrap: wrap;

                    .form-group {
                        flex: 1;
                        min-width: 48%;

                        &:last-child {
                            margin-right: 0;
                        }

                        @include m.tablet {
                            min-width: 45%;
                        }
                    }

                    .inputControl {
                        @include m.input(v.$blanco, #ecedec, v.$primario, v.$gri);
                    }

                    .labelControl {
                        font-size: 1.6rem;

                        @include m.tablet {
                            font-size: 2rem;
                            max-width: calc(100% - 40px);

                        }
                    }
                }


            }

            .informacionAcompaantes {
                .personal__titulo {
                    margin-bottom: 1rem;
                    font-size: 2rem;

                    @include m.tablet {
                        font-size: 2.5rem;
                    }
                }

                .form-group {
                    @include m.tablet {
                        max-width: 40rem;

                        input,
                        select {
                            min-width: 0;
                        }

                        select {
                            min-height: 4rem;
                        }
                    }
                }

                .inputControl {
                    @include m.input(v.$blanco, #ecedec, v.$primario, v.$gri);
                }

                .labelControl {
                    font-size: 1.6rem;

                    @include m.tablet {
                        font-size: 2rem;
                        max-width: calc(100% - 40px);

                    }
                }

                .botonAcompaante {
                    margin-bottom: 2rem;
                    background-color: v.$secundario;
                    color: v.$negro;
                    transition: v.$tran-btn;
                    padding: 0.5rem 1rem;
                    font-weight: 500;
                    text-decoration: none;
                    font-size: 16px;
                    letter-spacing: 0.1rem;
                    border: none;
                    border-radius: 1rem;
                    width: auto;
                    height: auto;
                    padding: 1rem 2rem;

                    @include m.tablet {
                        width: 30%;
                    }

                    &:hover {
                        background-color: v.$secundario-hover;
                        color: v.$blanco;
                        transition: v.$tran-btn;
                    }
                }

            }

            .informacionDispositivos {
                .personal__titulo {
                    margin-bottom: 1rem;
                    font-size: 2rem;

                    @include m.tablet {
                        font-size: 2.5rem;
                    }
                }

                .form-group {
                    @include m.tablet {
                        max-width: 40rem;

                        input,
                        select {
                            min-width: 0;
                        }

                        select {
                            min-height: 4rem;
                        }
                    }
                }

                .botonDispositivo {
                    margin-bottom: 2rem;
                    background-color: v.$secundario;
                    color: v.$negro;
                    transition: v.$tran-btn;
                    padding: 0.5rem 1rem;
                    font-weight: 500;
                    text-decoration: none;
                    font-size: 16px;
                    letter-spacing: 0.1rem;
                    border: none;
                    border-radius: 1rem;
                    width: auto;
                    height: auto;
                    padding: 1rem 2rem;

                    @include m.tablet {
                        width: 30%;
                    }

                    &:hover {
                        background-color: v.$secundario-hover;
                        color: v.$blanco;
                        transition: v.$tran-btn;
                    }
                }

                .inputControl {
                    @include m.input(v.$blanco, #ecedec, v.$primario, v.$gri);
                }

                .labelControl {
                    font-size: 1.6rem;

                    @include m.tablet {
                        font-size: 2rem;
                        max-width: calc(100% - 40px);

                    }
                }
            }

            .informacionAutomovil {
                .personal__titulo {
                    margin-bottom: 1rem;
                    font-size: 2rem;

                    @include m.tablet {
                        font-size: 2.5rem;
                    }
                }

                .form-group {
                    @include m.tablet {
                        max-width: 40rem;

                        input,
                        select {
                            min-width: 0;
                        }

                        select {
                            min-height: 4rem;
                        }
                    }
                }

                .botonAutomovil {
                    margin-bottom: 2rem;
                    background-color: v.$secundario;
                    color: v.$negro;
                    transition: v.$tran-btn;
                    padding: 0.5rem 1rem;
                    font-weight: 500;
                    text-decoration: none;
                    font-size: 16px;
                    letter-spacing: 0.1rem;
                    border: none;
                    border-radius: 1rem;
                    width: auto;
                    height: auto;
                    padding: 1rem 2rem;

                    @include m.tablet {
                        width: 30%;
                    }

                    &:hover {
                        background-color: v.$secundario-hover;
                        color: v.$blanco;
                        transition: v.$tran-btn;
                    }
                }

                .inputControl {
                    @include m.input(v.$blanco, #ecedec, v.$primario, v.$gri);
                }

                .labelControl {
                    font-size: 1.6rem;

                    @include m.tablet {
                        font-size: 2rem;
                        max-width: calc(100% - 40px);

                    }
                }
            }

            @media (max-width: 880px) {

                .NombreCompleto,
                .contacto,
                .identificacion,
                .DatosDispositivo {
                    flex-direction: column;

                    .form-group {
                        min-width: 100%;
                        margin-right: 0;
                        margin-bottom: 1rem;

                        &:last-child {
                            margin-bottom: 0;
                        }
                    }
                }
            }

            .form-check-input {
                min-width: 2rem;

                @include m.tablet {
                    min-width: 3rem;
                }
            }

            #BotonAgregar {
                display: flex;
                flex-direction: column;
                margin-top: 1.5rem;
            }

            .confirm__btn {
                @include m.button(v.$secundario, v.$secundario-hover, v.$negro, v.$blanco);
            }
        }
    }

}