@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.reuniones__titulo{
    font-size: 3.6rem;
    text-align: center;
    margin-bottom: 2.5rem;
}
.misReunionesGRID{
    display: grid;
    grid-template-columns: 1;
    gap: 2.5rem;

    @include m.tablet{
        grid-template-columns: repeat(3, 1fr);
    }
    .misReunionesGRID__card{
        .headerReunion, .datosReunion{
            background-color: v.$primario;
            padding: 2.4rem;
            box-shadow: 10px 10px 50px -10px rgba(0,0,0,0.75);
        }
        text-align: center;
        color: v.$blanco;
        .circleContainer{
            border-radius: 50%;
            background-color: v.$secundario;
            width: 3.5rem;
            height: 3.5rem;
            margin: 0 auto 1rem auto;
            .numReunion{
                font-size: 2.4rem;
                font-weight: bold;
                color: v.$blanco;
            }
        }
        .tituloReunion{
            font-size: 2rem;
            font-weight: bold;
        }
        .fechaReunion{
            color: v.$blanco;
            font-size: 2rem;
        }
        .datosReunion{
            background-color: v.$blanco;
            .horarioReunion{
                color: v.$negro;
                font-size: 1.8rem;
                font-weight: bold;
            }
            .anfitrionReunion{
                color: v.$negro;
                // font-weight: bold;
            }
        }
        .buttonReunion__Container{
            .buttonReunion{
                margin: 0 auto;
                @include m.button(v.$primario, v.$blanco, v.$blanco, v.$negro);
                width: 100% !important;
                margin-top: 0;
                border-top-left-radius: 0 !important;
                border-top-right-radius: 0 !important;
            }
        }
    }
}