@use 'Base/variables' as v;
@use 'Base/mixins' as m;

main.QR {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    text-align: center;


    #your-qr {
        width: 200px;
        height: 200px;
        background-color:v.$blanco;
        border: none;
        margin-bottom: 20px;
        text-align: center;
  

    }

    h1 {
        font-size: 24px;
        font-weight: bold;
        color: #333333;
        margin-bottom: 20px;
        text-align: center;
    }

    #my-qr {
        width: 800px;
        height: 800px;
        background-color:v.$blanco;
        border: none;
        text-align: center;

    }

}