@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.HeroInv{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 2rem;
    .HeroInvitado{
        justify-content: center;
        align-items: center;
        margin: auto;
        width: 90%;
        padding: 2rem;
        height:auto;
        display: block;
        background-color:v.$blanco;
        border-radius: 2%;
        box-shadow: 10px 10px 25px -4px #8b8b8bbf;
        h1{
            font-size: 3rem;
            text-align: center;
                @include m.desktop{
                    font-size: 4rem;
                }
        }
        @include m.tablet{
            
            height: auto;
            width: 70%;
        }
        @include m.desktop{
            display: flex;
        }
        .HeroInvitadoIzq{
            width: 300px;
            height: 300px;
            margin: auto;
            border-radius: 50%;
            overflow: hidden;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 2rem;
            margin-bottom: 2rem;
            @include m.tablet{
                width: 400px;
                height: 400px;
            }
            &__img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                overflow: hidden;  
                display: flex;
            }
        }
        .HeroInvitadoDer{
            width: 90%;
            @include m.tablet{
                width: 70%;
                margin: auto;
            }
            @include m.desktop{
                width: 50%;
            }
            &__Input{
                padding-top: 1rem;
                display: block;
                align-items: center;
                justify-content: space-between;
                margin-left: 10%;
                font-size: 1.4rem;
                width: 100%;
                
                &__Label{
                    padding-top: .8rem;
                    font-size: 1.8rem;
                    
                }
                input{
                    font-style:oblique;
                    font-size: 1.5rem;
                    margin-left:1rem;
                    border-radius: 10%;
                    border: none;
                    width: 80%;
                    height: 30px;
                    background-color: rgb(255, 255, 255);
                    
                }
                &__Ubi{
                    display:flex;
                    input{
                        width: 10rem;
                        margin-left: 0;
                    }
                }
            }      
        }     
    }
    
    .BotonesEyS{
        width: 80%;
        margin: auto;
        @include m.tablet{
            display: flex;
            justify-content: space-between;
            gap: 2rem;
            width: 50%;
            margin: auto;
        } 
        &__Entrada{
            @include m.button(v.$secundario, v.$secundario-hover, v.$negro, v.$blanco);
        }
    
        &__Salida{
            @include m.button(v.$primario, v.$primario-hover, v.$blanco, v.$blanco);
        }
        &__Guardar{
            @include m.button(v.$secundario, v.$secundario-hover, v.$negro, v.$blanco);
            margin:auto;
            width: 130px;
            text-align: center;
            @include m.tablet{
                width: 150px;
            }
        }
    }
    
    .HeroDispositivos{
        padding-top: 3rem;
        width: 70%;
        display: block;
        margin: auto;
        th{
            background-color: v.$secundario;
        }
            h1{
                text-align: center;
                font-size: 3rem;
            }
    }
    
    .HeroEntradasSalidas{
        padding-top: 3rem;
        width: 70%;
        display: block;
        margin: auto;
        th{
            background-color: v.$secundario;
        }
        h1{
            text-align: center;
            font-size: 3rem;
        }
        .icon-trash {
            width: 40px;
            height: 30px;
            background: none;
            border: none;
        }
        .icon-trash img {
            width: 100%;
            height: 100%;
        }
    }
}

