@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.consultarDetallesReunion {
    .consultar_titulo{
        margin-bottom: 1.3rem;
        @media (max-width: 832px) {
            margin-bottom: 0.8rem;
        }
        @media (max-width: 582px) {
            margin-bottom: 0.6rem;
        }
    }
    .botones {
        display: flex;
        justify-content: center;
        align-items: center;
        align-content: center;
        flex-wrap: wrap;

        button {
            transition: v.$tran-btn;
            font-weight: 500;
            text-decoration: none;
            font-size: 15px;
            letter-spacing: 0.1rem;
            vertical-align: middle;
            border: none;
            border-radius: 1rem;
            padding: 1rem 2rem;
        }

        .reagendar_btn,
        .reagendar30m_btn,
        .reagendar1h_btn {
            margin: 2.5rem 1.5rem 0rem 1.5rem;
            width: 28%;
            height: 4rem;
            background-color: v.$secundario;
            color: v.$negro;

            @media (max-width: 832px) {
                font-size: 13px;
                height: 5rem;
                padding: 0.6rem 1.6rem;
            }
            @media (max-width: 582px) {
                margin: 1.5rem 1rem 0rem 1rem;
            }
            @media (max-width: v.$phone) {
                height: 4rem;
                font-size: 11px;
                padding: 0.5rem 1rem;
            }

            &:hover {
                background-color: v.$secundario-hover;
                color: v.$blanco;
                transition: v.$tran-btn;
            }
        }


        .cancelar_btn {
            margin: 1.5rem 1rem 0rem 1rem;
            width: 50%;
            height: 4rem;
            background-color: v.$secundario;
            color: v.$negro;

            @media (max-width: 582px) {
                width: 84%;
                height: 4rem;
                padding: 0.6rem 1.6rem;
            }

            @media (max-width: v.$phone) {
                height: 4rem;
                font-size: 11px;
                padding: 0.5rem 1rem;
            }

            &:hover {
                background-color: v.$secundario-hover;
                color: v.$blanco;
                transition: v.$tran-btn;
            }
        }

        .regresar_btn {
            margin: 1.5rem 1.5rem 0rem 1.5rem;
            width: 35%;
            height: 4rem;
            background-color: v.$primario;
            color: v.$blanco;

            @media (max-width: 582px) {
                width: 84%;
                height: 4rem;
                margin: 1.5rem 1rem 0rem 1rem;
                padding: 0.6rem 1.6rem;
            }

            @media (max-width: v.$phone) {
                width: 80%;
                height: 4rem;
                font-size: 11px;
                padding: 0.5rem 1rem;
            }

            &:hover {
                background-color: v.$primario-hover;
                color: v.$blanco-hover;
                transition: v.$tran-btn;
            }
        }
    }

    .Detalle {
        //Div
        background-color: v.$blanco;
        padding: 4rem;
        box-shadow: 10px 10px 8px -3px rgba(0, 0, 0, 0.33);

        .DetalleReunion {
            //Form
            display: flex;
            flex-wrap: wrap;
            margin-bottom: 2rem;
            border: none;
            padding: 1.5rem;

            @media (max-width: v.$tablet) {
                font-size: 1.5rem;
                margin-left: 3rem;
                width: inherit;
                display: contents;
                justify-content: center;
                align-items: center;

                ._idReu,
                ._tituloReu,
                ._fecha,
                ._horario {
                    width: 100%;

                    label {
                        margin-top: 1rem;
                    }

                    .guion {
                        display: none;
                    }
                }
            }

            @media (max-width: v.$phone) {
                font-size: 1.2rem;
                width: inherit;
                display: flex;
                align-items: flex-start;
                flex-wrap: nowrap;
                flex-direction: column;

                ._idReu,
                ._tituloReu,
                ._fecha,
                ._horario {
                    width: 100%;
                }

                label {
                    margin-top: 1rem;
                }

                input {
                    width: 70%;
                    max-width: 100%;
                    white-space: nowrap;
                    overflow: auto;
                }
            }

            //Secciones del form
            ._idReu,
            ._tituloReu,
            ._fecha,
            ._horario {
                width: 50%;

                label {
                    margin-top: 1.5rem;
                }
            }

            ._tituloReu {
                #reunionTitulo {
                    width: max-content;
                    min-width: 30rem;
                    max-width: 100%;
                    white-space: nowrap;
                    overflow: auto;
                }
            }

            ._nombreSala {
                #sala {
                    width: 18rem;
                    min-width: 20%;
                    max-width: 100%;
                    white-space: nowrap;
                    overflow: auto;
                }
            }

            //Ajuste de inputs especficos
            #salaNumero,
            #salaPiso {
                width: 5rem;
            }

            #horarioInicio {
                width: 10rem;
            }

            //Ajustes generales del form
            label {
                margin-top: 1.5rem;
                font-weight: bold;
            }

            input {
                border: none;
                background-color: v.$blanco;
            }
        }

        //Configuracin de la tabla de invitados
        .__Tabla {
            @media (max-width: v.$tablet) {
                font-size: 1.4rem;
            }

            @media (max-width: v.$phone) {
                font-size: 1.1rem;
            }

            .tabla {
                .tabla__cabecera {
                    tr {
                        .cabecera {
                            background-color: v.$tabla-cabecera;
                            color: v.$tabla-cabecera-txt;
                            width: fit-content;
                            padding: 1rem;
                            text-align: center;
                            vertical-align: middle;
                        }

                        .cabecera:hover {
                            background-color: v.$tabla-cabecera-hover;
                        }
                    }
                }

                .tabla__cuerpo {
                    tr {

                        .nombreInvi,
                        .correo {
                            text-align: center;
                            vertical-align: middle;
                            padding: 1rem 4rem 1rem 4rem;

                            @media (max-width: v.$phone) {
                                padding: 1rem;
                            }
                        }
                    }
                }
            }
        }
    }
}