@use 'Base/variables' as v;
@use 'Base/mixins' as m;
.contenedorUser{
    display:block;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin: 0;
    background-color: white;
    border-radius: 10px;
    box-shadow: 10px 10px 25px -4px rgba(139, 139, 139, 0.75);
        @include m.phone{
            margin-top: 0;
        }
        @include m.desktop{
            padding-left: 0;
            display: flex;
        }
    &__Info{
        margin: auto;
        display: block;
        position: flex;
        // background-color: #ff9898;
            @include m.tablet{
                width: 90%;
                    input{
                        width: 80%;
                    }
            }
            @include m.desktop{
                width: 70%;
            }
    }
    &__left{
        position: relative;
        margin: 5%;
        height: 300px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding-top: 5%;
        @include m.tablet{
            margin: auto;
            width: 50rem;
            height: 50rem;
            padding-top: 0%;
        }

        &__circle{
            position:absolute;
            left: 50%;
            transform: translateX(-50%);
            width: 300px;
            height: 300px;
            border-radius: 50%;
            overflow: hidden;
            display: flex;
            justify-content: center;
            align-items: center;
            margin: auto;
            background-color: v.$gris;
            @include m.tablet{
                position: flex;
                padding-top: 0%;
                width: 350px;
                height:  350px;
            }

            img{      
                width: 100%;
                height: 100%;
                object-fit: cover;
                overflow: hidden;  
                display: flex;
            }
            input{
                display: none;
            }

            &__icon{
                position: absolute;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 100%;
                height: 100%;
                border-radius: 50%;
                transition: all 500ms ease-out;
                    img{
                        width: 75px;
                        height: 75px;
                        display: none;
                        @include m.tablet{
                            height: 100px;
                            width: 100px;
                        }
                        @include m.desktop{
                           
                        }
                    }
                &:hover{
                    background: rgba($color: #000000, $alpha: 0.5);
                    display: flex;
                    img{
                        display: flex;
                        cursor: pointer;
                    }
                }
                //display: none;
            }

         
        }

       
    }
    
}

